.tab-list {  
  border-bottom: 1px solid #F5F5F5;
  padding-left: 0;
}

.tab-list-item {
  display: inline-block;
  list-style: none;
  margin-bottom: -1px;
  padding: 0.5rem 0.75rem;
  font-size: 1.05em;
  font-weight: bold;
  color: #999;
  cursor: pointer;
  &:hover {
    color: #3b3fd8;
  }
}

.tab-list-active {
  //background-color: white;
  color: #3b3fd8;
  border: 1px solid #F5F5F5;
  border-width: 1px 1px 0 1px;
  border-top: 2px solid #ffa200;
  border-bottom: 1px solid #fff;
}